.container {
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 20px;
}

.cellContainer {
    border: #282c34 1px solid;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    height: auto;
    margin-top: 5px;
}

.indexContainer {
    text-align: center;
    line-height: 150px;
    height: 150px;
    width: 10%;
    background-color: #108EE9;
    border-radius: 10px;
    color: white;
}

.middleContainer {
    height: 80px;
    width: 80%;
    padding: 5px;
}

.rightContainer {
    text-align: center;
    line-height: 150px;
    height: 150px;
    width: 10%;
    background-color: #f5f5f9;
    border-radius: 10px;
    border-left: #282c34 1px solid;
}

.textSpan {
    margin-left: 10px;
    word-break: normal;
    word-wrap: break-word;
    display: inline-block;
    white-space:pre-wrap;
}

.textSpanBold {
    margin-left: 10px;
    word-break: normal;
    word-wrap: break-word;
    display: inline-block;
    white-space:pre-wrap;
    font-weight: bold;
}

:global(.am-list-item) {
    background-color: #f5f5f9;
    padding-left: 0px;
}

.declineButton {
    margin-top: 10px;
    margin-right: 10px;
    /*margin: 0 auto;*/
    /*padding: 0 0;*/
    border-radius: 5px;
    background-color: red;
    color: white;
    height: 40px;
    width: 200px;
    font-size: 16px;
    align-items: center;

}

.agreeButton {
    margin-top: 10px;
    margin-right: 10px;
    /*margin: 0 auto;*/
    /*padding: 0 0;*/
    border-radius: 5px;
    background-color: #108EE9;
    color: white;
    height: 40px;
    width: 200px;
    font-size: 16px;
    align-items: center;

}

.txContainer {
    margin-top: 30px;
    border: 1px solid grey;
    height: 200px;
    width: 50%;
    word-wrap : break-word;
    padding: 10px;
}